:root {
  --white-color: #fff;
  --black-color: #000;
  --green-bg-color: #84a178;
  --green-product-border-color: #96af8c;
  --white-product-modal-color: #d9d9d9;
  --green-product-modal-color: rgba(156, 190, 143, .7);
  --orange-product-modal-color: rgba(213, 117, 63, .7);
  --product-modal-shadow-color: rgba(212, 20, 67, .1);
  --light-green-products-bg-color: #aec6a5;
  --green-products-bg-color: #9cbe8f;
  --yellow-products-bg-color: #efd478;
  --orange-products-bg-color: #d6936d;
  --green-reviews-color: #84a178;
  --gallery-bg-color: #f5f5f5;
  --modal-form-input-color: rgba(248, 210, 78, .1);
  --white-content-color: #fcfcfc;
  --black-content-color: #131313;
  --yellow-content-color: #f8d24e;
  --green-content-color: #0a8817;
  --about-yellow-color: rgba(248, 210, 78, .7);
  --contacts-border-color: #e1e1e1;
  --contacts-color-text-time: #ababab;
  --contacts-bg-card-color: #fff;
  --footer-copyright-color: #7a7a7a;
  --footer-copyright-border: #e1e1e1;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --transition-dur-and-func-btns: 1000ms ease-in-out;
  --transition-modal-window: transform 1500ms ease;
}

.section__about {
  background-color: var(--green-bg-color);
  padding-top: 60px;
  padding-bottom: 42px;
}

.about__container {
  width: 100%;
}

.section__label {
  letter-spacing: .04em;
  text-align: center;
  text-transform: uppercase;
  color: var(--white-content-color);
  margin-bottom: 12px;
  font-size: 18px;
  font-weight: 700;
  transition: transform .8s;
  transform: scale(0, 1);
}

.section__label._active {
  transform: scale(1);
}

.section__title {
  letter-spacing: .04em;
  text-align: center;
  text-transform: uppercase;
  color: var(--white-content-color);
  width: 200px;
  opacity: 0;
  margin-bottom: 32px;
  margin-left: auto;
  margin-right: auto;
  font-family: seymour one;
  font-size: 30px;
  font-weight: 400;
  transition: transform .8s .3s, opacity .8s .3s;
  display: block;
  transform: translate(0, 120%);
}

.section__title._active {
  opacity: 1;
  transform: translate(0);
}

.about__image-pad {
  width: 278px;
  height: 278px;
  background-color: var(--about-yellow-color);
  border-radius: 50%;
  margin-bottom: 62px;
  margin-left: auto;
  margin-right: auto;
  transition: transform .8s;
  position: relative;
  transform: scale(0);
}

.about__image-pad._active {
  transform: scale(1);
}

.about__picture {
  position: absolute;
  top: 22px;
}

.about__description {
  width: 220px;
  letter-spacing: .04em;
  text-align: center;
  color: var(--white-content-color);
  opacity: 0;
  margin-bottom: 4px;
  margin-left: auto;
  margin-right: auto;
  font-size: 12px;
  font-weight: 400;
  transition: transform .8s, opacity .8s;
  transform: translate(0, 100%);
}

.about__description._active {
  opacity: 1;
  transform: translate(0);
}

.about__main-text {
  width: 220px;
  letter-spacing: .04em;
  text-align: center;
  color: var(--white-content-color);
  opacity: 0;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  font-size: 12px;
  font-weight: 400;
  transition: transform .8s, opacity .8s;
  transform: translate(0, 100%);
}

.about__main-text._active {
  opacity: 1;
  transform: translate(0);
}

.about__main-text--tablet {
  letter-spacing: .04em;
  text-align: center;
  color: var(--white-content-color);
  opacity: 0;
  margin-bottom: 48px;
  margin-left: auto;
  margin-right: auto;
  font-size: 12px;
  font-weight: 400;
  transition: transform .8s, opacity .8s;
  display: none;
  transform: translate(0, 100%);
}

.about__main-text--tablet._active {
  opacity: 1;
  transform: translate(0);
}

.about__button {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

