.section__advantages {
  background-color: var(--green-bg-color);
  padding-bottom: 60px;
}

.advantages__container {
  width: 100%;
}

.advantages__item {
  transition: transform .8s;
  transform: scale(-1, 0);
}

.advantages__item:before {
  content: "";
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  background-color: var(--yellow-content-color);
  background-position: 50%;
  border-radius: 50%;
  margin-bottom: 16px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.advantages__item:not(:last-child) {
  margin-bottom: 22px;
}

.advantages__item._active {
  transform: scale(1);
}

.advantages__item--cow:before {
  background-image: url("advantages-cow.0da04067.png");
}

.advantages__item--weight:before {
  background-image: url("advantages-weight.5732aa7c.png");
}

.advantages__item--ice-cream:before {
  background-image: url("advantages-ice-cream.90b5388a.png");
}

.advantages__value {
  letter-spacing: .04em;
  color: var(--white-content-color);
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 18px;
  font-family: roboto;
  font-size: 36px;
  font-weight: 700;
}

.advantages__description {
  letter-spacing: .04em;
  color: var(--white-content-color);
  text-align: center;
  width: 230px;
  margin-left: auto;
  margin-right: auto;
  font-family: Montserrat;
  font-size: 12px;
  font-weight: 700;
  display: block;
}

