.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.contacts__item {
  background-color: var(--white-content-color);
  opacity: 0;
  border-radius: 24px;
  transition: transform .8s;
  transform: translate(0, 20%)scale(.5);
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

.contacts__item._active {
  opacity: 1;
  transform: translate(0)scale(1);
}

.contacts__text {
  background-color: var(--yellow-content-color);
  color: var(--black-content-color);
  border-radius: 4px;
  margin-bottom: 20px;
  padding-left: 24px;
  padding-right: 24px;
  font-size: 12px;
  font-weight: 500;
  display: inline-block;
}

.address {
  margin-bottom: 48px;
}

.address__text {
  color: var(--black-content-color);
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 500;
}

.address__link {
  color: var(--orange-products-bg-color);
  transition: border-color 400ms var(--timing-function);
  border-bottom: 1px solid rgba(0, 0, 0, 0);
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  position: relative;
}

.address__link:hover, .address__link:focus {
  color: var(--orange-products-bg-color);
  border-color: var(--orange-products-bg-color);
  text-decoration: none;
}

.work-list {
  margin-bottom: 48px;
  position: relative;
}

.work-list__item:not(:last-child) {
  margin-bottom: 8px;
}

.work-list__day {
  color: var(--black-content-color);
  font-size: 12px;
  font-weight: 500;
}

.work-list__day:after {
  content: "";
  width: 100%;
  border: 1px solid var(--contacts-border-color);
  background-color: var(--contacts-border-color);
  position: absolute;
  top: -24px;
  left: 0;
}

.work-list__time {
  color: var(--contacts-color-text-time);
  font-size: 12px;
  font-weight: 500;
}

.work-list__time:after {
  content: "";
  width: 100%;
  border: 1px solid var(--contacts-border-color);
  background-color: var(--contacts-border-color);
  position: absolute;
  bottom: -24px;
  left: 0;
}

.contacts-page__info {
  color: var(--black-content-color);
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

