.footer {
  background-color: var(--green-bg-color);
  color: var(--white-content-color);
}

.footer__title {
  text-transform: uppercase;
  display: block;
}

.footer__title--small {
  opacity: 0;
  transition: transform .8s;
  transform: translate(30%)scale(.4);
}

.footer__title--small._active {
  opacity: 1;
  transform: translate(0)scale(1);
}

.footer__title--big {
  opacity: 0;
  transition: transform .8s;
  transform: translate(30%)scale(.4);
}

.footer__title--big._active {
  opacity: 1;
  transform: translate(0)scale(1);
}

.footer-social-networks__list {
  justify-content: space-between;
  display: flex;
}

.footer-social-networks__icon {
  width: 34px;
  height: 34px;
  transition: width 400ms var(--timing-function), height 400ms var(--timing-function);
  border-radius: 50%;
}

.footer-social-networks__icon:hover {
  width: 32px;
  height: 32px;
}

.footer-social-networks__icon:focus {
  width: 35px;
  height: 35px;
}

.footer-social-networks__icon--green {
  fill: var(--green-bg-color);
  transition: fill 400ms var(--timing-function);
}

.footer-social-networks__icon:hover .footer-social-networks__icon--green, .footer-social-networks__icon:focus .footer-social-networks__icon--green {
  fill: var(--white-color);
}

.footer__text {
  color: var(--black-content-color);
  text-align: right;
}

.footer__link {
  color: var(--white-content-color);
}

.footer-copyright {
