.sections-bg-image {
  background-image: linear-gradient(to bottom, transparent 0% 44.8%, var(--green-bg-color) 44.8% 100%), url("ice-cream-bg4.f49204cb.jpg");
  background-position: 50% 111px;
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: auto;
