html {
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  letter-spacing: 0.04em;
}

h2,
h3,
p {
  margin: 0;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.container {
  padding: 0;
  margin-right: auto;
  margin-left: auto;
}

@media screen and (min-width: 320px) {
  .container {
    width: calc(100% - 30px);
  }
}
