:root {
  --white-color: #fff;
  --black-color: #000;
  --green-bg-color: #84a178;
  --green-product-border-color: #96AF8C;
  --white-product-modal-color: #d9d9d9;
  --green-product-modal-color: #9cbe8fb3;
  --orange-product-modal-color: rgba(213, 117, 63, 0.7);
  --product-modal-shadow-color: rgba(212, 20, 67, 0.1);
  --light-green-products-bg-color: #aec6a5;
  --green-products-bg-color: #9cbe8f;
  --yellow-products-bg-color: #efd478;
  --orange-products-bg-color: #d6936d;
  --green-reviews-color: rgba(132, 161, 120, 1);
  --gallery-bg-color: #F5F5F5;
  --modal-form-input-color: rgba(248, 210, 78, 0.1);
  --white-content-color: #fcfcfc;
  --black-content-color: #131313;
  --yellow-content-color: #f8d24e;
  --green-content-color: #0A8817;
  --about-yellow-color: rgba(248, 210, 78, 0.7);
  --contacts-border-color: #e1e1e1;
  --contacts-color-text-time: #ababab;
  --contacts-bg-card-color: #ffffff;
  --footer-copyright-color: #7A7A7A;
  --footer-copyright-border: #E1E1E1;
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --transition-dur-and-func-btns: 1000ms ease-in-out;
  --transition-modal-window: transform 1500ms ease;
}

.section__about {
  background-color: var(--green-bg-color);
  padding-top: 60px;
  padding-bottom: 42px;
}

.about__container {
  width: 100%;
}

.section__label {
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.04em;
  text-align: center;
  text-transform: uppercase;
  color: var(--white-content-color);
  margin-bottom: 12px;
  transform: scale(0, 1);
  transition: transform 0.8s ease 0s;
}

.section__label._active {
  transform: scale(1, 1);
}

.section__title {
  display: block;
  margin-left: auto;
  margin-right: auto;
  font-family: "seymour one";
  font-size: 30px;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-align: center;
  text-transform: uppercase;
  color: var(--white-content-color);
  width: 200px;
  margin-bottom: 32px;
  transform: translate(0px, 120%);
  opacity: 0;
  transition: transform 0.8s ease 300ms, opacity 0.8s ease 300ms;
}

.section__title._active {
  transform: translate(0px, 0px);
  opacity: 1;
}

.about__image-pad {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  width: 278px;
  height: 278px;
  background-color: var(--about-yellow-color);
  border-radius: 50%;
  margin-bottom: 62px;
  transform: scale(0);
  transition: transform 0.8s ease 0ms;
}

.about__image-pad._active {
  transform: scale(1);
}

.about__picture {
  position: absolute;
  top: 22;
}

.about__description {
  width: 220px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-align: center;
  color: var(--white-content-color);
  transform: translate(0px, 100%);
  opacity: 0;
  transition: transform 0.8s ease 0ms, opacity 0.8s ease 0ms;
}

.about__description._active {
  transform: translate(0px, 0px);
  opacity: 1;
}

.about__main-text {
  width: 220px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-align: center;
  color: var(--white-content-color);
  transform: translate(0px, 100%);
  opacity: 0;
  transition: transform 0.8s ease 0ms, opacity 0.8s ease 0ms;
}

.about__main-text._active {
  transform: translate(0px, 0px);
  opacity: 1;
}

.about__main-text--tablet {
  display: none;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 48px;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-align: center;
  color: var(--white-content-color);
  transform: translate(0px, 100%);
  opacity: 0;
  transition: transform 0.8s ease 0ms, opacity 0.8s ease 0ms;
}

.about__main-text--tablet._active {
  transform: translate(0px, 0px);
  opacity: 1;
}

.about__button {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 320px) {
  .about__container {
    width: 320px;
