    letter-spacing: 0.04em;
  }
}
@media screen and (min-width: 1200px) {
  .section__advantages {
    padding-bottom: 86px;
  }
  .advantages__container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
  .advantages__list {
    justify-content: space-between;
    gap: 0px;
  }
  .advantages__item {
    flex-basis: calc((100% - 96px) / 3);
  }
