.contacts {
  padding-bottom: 40px;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  margin: -1px;
}

.contacts__item {
  background-color: var(--white-content-color);
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  transform: translate(0px, 20%) scale(0.5);
  opacity: 0;
  transition: transform 0.8s ease 0s;
}

.contacts__item._active {
  transform: translate(0px, 0px) scale(1);
  opacity: 1;
}

.contacts__text {
  display: inline-block;
  background-color: var(--yellow-content-color);
  border-radius: 4px;
  padding-left: 24px;
  padding-right: 24px;
  font-weight: 500;
  font-size: 12px;
  color: var(--black-content-color);
  margin-bottom: 20px;
}

.address {
  margin-bottom: 48px;
}

.address__text {
  font-weight: 500;
  font-size: 12px;
  color: var(--black-content-color);
  margin-bottom: 8px;
}

.address__link {
  position: relative;
  font-weight: 500;
  font-size: 12px;
  color: var(--orange-products-bg-color);
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: border-color 400ms var(--timing-function);
}
.address__link:hover, .address__link:focus {
  text-decoration: none;
  color: var(--orange-products-bg-color);
  border-color: var(--orange-products-bg-color);
}

.work-list {
  margin-bottom: 48px;
  position: relative;
}

.work-list__item:not(:last-child) {
  margin-bottom: 8px;
}

.work-list__day {
  font-weight: 500;
  font-size: 12px;
  color: var(--black-content-color);
}
.work-list__day::after {
  content: "";
  position: absolute;
  width: 100%;
  border: 1px solid var(--contacts-border-color);
  background-color: var(--contacts-border-color);
  left: 0;
  top: -24px;
}

.work-list__time {
  font-weight: 500;
  font-size: 12px;
  color: var(--contacts-color-text-time);
}
.work-list__time::after {
  content: "";
  position: absolute;
  width: 100%;
  border: 1px solid var(--contacts-border-color);
  background-color: var(--contacts-border-color);
  left: 0;
  bottom: -24px;
}

.contacts-page__info {
  font-weight: 500;
  font-size: 12px;
  color: var(--black-content-color);
  text-decoration: none;
}

@media screen and (max-width: 767px) {
